import {BrowserModule} from '@angular/platform-browser';
import {ErrorHandler, NgModule} from '@angular/core';
import {IonicApp, IonicErrorHandler, IonicModule} from 'ionic-angular';
import {SplashScreen} from '@ionic-native/splash-screen';
import {StatusBar} from '@ionic-native/status-bar';
import {MyApp} from './app.component';
import {SettingsProvider} from '../providers/settings/settings';
import {Network} from '@ionic-native/network';
import {InAppBrowser} from '@ionic-native/in-app-browser';
import {ScreenOrientation} from '@ionic-native/screen-orientation';
import {HeaderColor} from '@ionic-native/header-color';
import {File} from '@ionic-native/file';
import {FileTransfer} from '@ionic-native/file-transfer';
import {Camera} from '@ionic-native/camera';

//
// Pages
//
import {HomePage} from '../pages/home/home';
import {SettingsPage} from "../pages/settings/settings";
import {SourcesPage} from "../pages/sources/sources";
import {ProfielPage} from "../pages/profiel/profiel";
import {SportPage} from "../pages/sport/sport";
import {EconomiePage} from "../pages/economie/economie";
import {AutoPage} from "../pages/auto/auto";
import {MisdaadPage} from "../pages/misdaad/misdaad";
import {TechPage} from "../pages/tech/tech";
import {AdminPage} from "../pages/admin/admin";
import {AboutPage} from "../pages/about/about";
import {CommentsPage} from "../pages/comments/comments";


//
// Modules
//
import {NieuwsPageModule} from "../pages/nieuws/nieuws.module";
import {CategoryPageModule} from "../pages/category/category.module";
import {HttpClientModule} from '@angular/common/http';
import {ReactiveFormsModule} from '@angular/forms';
import {IonicStorageModule} from '@ionic/storage';
import {FavorietenPageModule} from "../pages/favorieten/favorieten.module";
import {FeedPageModule} from "../pages/feed/feed.module";
import {RegisterPageModule} from "../pages/register/register.module";
import {TutorialPageModule} from "../pages/tutorial/tutorial.module";
import {SettingsPageModule} from "../pages/settings/settings.module";
import {SportPageModule} from "../pages/sport/sport.module";
import {EconomiePageModule} from "../pages/economie/economie.module";
import {AutoPageModule} from "../pages/auto/auto.module";
import {MisdaadPageModule} from "../pages/misdaad/misdaad.module";
import {TechPageModule} from "../pages/tech/tech.module";
import {AdminPageModule} from "../pages/admin/admin.module";
import {SourcesPageModule} from "../pages/sources/sources.module";
import {ProfielPageModule} from "../pages/profiel/profiel.module";
import {AboutPageModule} from "../pages/about/about.module";
import {CommentsPageModule} from "../pages/comments/comments.module";


//
// Providers
//
import {InfouserProvider} from '../providers/infouser/infouser';

@NgModule({
    declarations: [
        MyApp,
        HomePage
        // SettingsPage,
        // SportPage,
        // EconomiePage,
        // AutoPage,
        // MisdaadPage,
        // TechPage,
        // AdminPage,
        // SourcesPage,
        // ProfielPage,
        // AboutPage,
        // CommentsPage
    ],
    imports: [
        BrowserModule,
        HttpClientModule,
        IonicModule.forRoot(MyApp, {
            mode: "md",
            scrollAssist: false,
            autoFocusAssist: false
        }, 
{
  links: [
    { loadChildren: '../pages/admin/admin.module.ngfactory#AdminPageModuleNgFactory', name: 'AdminPage', segment: 'admin', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/about/about.module.ngfactory#AboutPageModuleNgFactory', name: 'AboutPage', segment: 'about', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/auto/auto.module.ngfactory#AutoPageModuleNgFactory', name: 'AutoPage', segment: 'auto', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/comments/comments.module.ngfactory#CommentsPageModuleNgFactory', name: 'CommentsPage', segment: 'comments', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/economie/economie.module.ngfactory#EconomiePageModuleNgFactory', name: 'EconomiePage', segment: 'economie', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/feed/feed.module.ngfactory#FeedPageModuleNgFactory', name: 'FeedPage', segment: 'feed', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/misdaad/misdaad.module.ngfactory#MisdaadPageModuleNgFactory', name: 'MisdaadPage', segment: 'misdaad', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/category/category.module.ngfactory#CategoryPageModuleNgFactory', name: 'CategoryPage', segment: 'category', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/nieuws/nieuws.module.ngfactory#NieuwsPageModuleNgFactory', name: 'NieuwsPage', segment: 'nieuws', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module.ngfactory#SettingsPageModuleNgFactory', name: 'SettingsPage', segment: 'settings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sources/sources.module.ngfactory#SourcesPageModuleNgFactory', name: 'SourcesPage', segment: 'sources', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sport/sport.module.ngfactory#SportPageModuleNgFactory', name: 'SportPage', segment: 'sport', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tech/tech.module.ngfactory#TechPageModuleNgFactory', name: 'TechPage', segment: 'tech', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tutorial/tutorial.module.ngfactory#TutorialPageModuleNgFactory', name: 'TutorialPage', segment: 'tutorial', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/vermaak/vermaak.module.ngfactory#VermaakPageModuleNgFactory', name: 'VermaakPage', segment: 'vermaak', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profiel/profiel.module.ngfactory#ProfielPageModuleNgFactory', name: 'ProfielPage', segment: 'profiel', priority: 'low', defaultHistory: [] }
  ]
}),
        ReactiveFormsModule,
        IonicStorageModule.forRoot(),
        FavorietenPageModule,
        FeedPageModule,
        RegisterPageModule,
        NieuwsPageModule,
        CategoryPageModule,
        TutorialPageModule,
        SettingsPageModule,
        SportPageModule,
        EconomiePageModule,
        AutoPageModule,
        MisdaadPageModule,
        TechPageModule,
        AdminPageModule,
        SourcesPageModule,
        ProfielPageModule,
        AboutPageModule,
        CommentsPageModule
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
        HomePage,
        SettingsPage,
        SportPage,
        EconomiePage,
        AutoPage,
        MisdaadPage,
        TechPage,
        AdminPage,
        SourcesPage,
        ProfielPage,
        AboutPage,
        CommentsPage
    ],
    providers: [
        StatusBar,
        SplashScreen,
        HttpClientModule,
        {provide: ErrorHandler, useClass: IonicErrorHandler},
        SettingsProvider,
        Network,
        SettingsProvider,
        InAppBrowser,
        ScreenOrientation,
        HeaderColor,
        Camera,
        File,
        FileTransfer,
        InfouserProvider
    ]

})
export class AppModule {

}



